:root {
  --color-dark-grey: #10100e;
  --color-white: #fff;
  --color-black: #000;
  --color-yellow: #ffff00;
  --color-blue: #6a83fb;
  --color-violet: #ae72c2;
  --color-red: #f75d83;

  --spacing-ultrasmall: 5px;
  --spacing-small: 10px;
  --spacing-medium: 20px;
  --spacing-large: 30px;

  --border-radius-small: 3px;
  --border-radius-medium: 5px;

  --font-family-main: sans-serif;
  --font-size-header: 26px;
  --font-size-subheader: 18px;
  --font-size-main: 16px;
  --font-bold: 600;

  --width-max: 1200px;

  --margin-list: 7px;

  --text-shadow-yellow: 0 0 2px var(--color-white), 0 0 40px var(--color-yellow);
  --text-shadow-blue: 0 0 2px var(--color-white), 0 0 40px var(--color-blue);

  --box-shadow-white: 0 16px 24px 2px rgba(255, 255, 255, 0.02),
    0 6px 30px 5px rgba(255, 255, 255, 0.04),
    0 8px 10px -5px rgba(255, 255, 255, 0.1);

  --box-shadow-black: 0 16px 24px 2px rgba(0, 0, 0, 0.5),
    0 6px 30px 5px rgba(0, 0, 0, 0.1), 0 8px 10px -5px rgba(0, 0, 0, 0.2);

  --theme-light-header: #dfe2df;
  --theme-dark-header: #ca2c31;
  --theme-neutral-header: #cccc46;
  --theme-default-header: var(--theme-neutral-header);

  --theme-light-bgimage: url(@static/lightside.jpg);
  --theme-dark-bgimage: url(@static/darkside.jpg);
  --theme-neutral-bgimage: url(@static/bg.jpg);
  --theme-default-bgimage: var(--theme-neutral-bgimage);

  --transition: 0.4s;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--color-dark-grey);
  font-family: sans-serif;
  font-size: var(--font-size-main);
  background-image: var(--theme-default-bgimage);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top left;
  background-attachment: fixed;
}

img {
  display: block;
}

.header__text {
  line-height: 1.7;
  font-size: var(--font-size-header);
  text-shadow: 0 0 2px var(--color-black);
  color: var(--theme-default-header);
  transition: color var(--transition);
}

*, *:after, *:before {
  box-sizing: border-box;
}
